body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* styles.css */
ul {
  list-style-type: none;
  padding-left: 20px; /* Indentation for hierarchy */
}

ul > li {
  margin: 5px 0;
  width: fit-content;
  margin: 5px 0;
  padding: 10px;
  border-left: 3px solid #007BFF;
}

a {
  text-decoration: none;
  color: #007BFF;
}

a:hover {
  text-decoration: underline;
}

button {
  margin-bottom: 10px;
}


/* styles.css */

/* Default layout for larger screens */
.container {
  display: flex;
  flex-direction: row;
}

.sidebar {
  width: 25%;
  padding: 10px;
  border-right: 1px solid #ccc;
}

.content {
  width: 75%;
  padding: 10px;
}

/* Mobile layout */
@media (max-width: 768px) {
  .container {
      flex-direction: column;
  }

  .sidebar {
      width: 100%;
      border-right: none;
      border-bottom: 1px solid #ccc;
  }

  .content {
      width: 100%;
  }
}



/* styles.css */

code {
  background-color: #f5f5f5;
  color: #d6336c;
  padding: 2px 4px;
  border-radius: 4px;
  font-size: 90%;
}

pre {
  padding: 10px;
  background-color: #2e3440;
  color: #d8dee9;
  border-radius: 5px;
  overflow: auto;
}

h1, h2, h3 {
  color: #007BFF;
}

ul, ol {
  padding-left: 20px;
}

a {
  color: #007BFF;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}
mark {
  background-color: #ffeb3b; /* Light yellow */
  color: #000;
  padding: 0 4px;
  border-radius: 3px;
}


/* side bar :start */






.sidebar-logo {
  font-size: 1.5em;
  font-weight: bold;
  color: #333;
  border: solid thin #ccc;
  width: fit-content;
  padding: 0 10px;
  border-radius: 20px 0 ;
  background-color: aqua;
  border-left: solid thick;
  border-right: none;
}

.sidebar-slogan {
  font-size: .8em;
  color: #333;
  margin-top: -18px;
  margin-left: 30px;
  border-top: solid thick #ccc;
  width:fit-content;
}

.sidebar {
  width: 25%; /* Fixed width for the sidebar */
  position: sticky; /* Makes the sidebar sticky */
  top: 0; /* Sticks the sidebar to the top of the viewport */
  height: 100vh; /* Full height of the viewport */
  overflow-y: auto; /* Scrollable if content exceeds the height */
  overflow-x: auto  /* Scrollable if content exceeds the width */;
  border-right: 1px solid #ccc; /* Right border for separation */
  background-color: #f8f9fa; /* Light gray background */
  padding: 10px; /* Padding for content inside the sidebar */
}

.main-content {
  width: 75%; /* Remaining width for the main content */
  padding: 10px; /* Padding for the content */
  overflow-y: auto; /* Scrollable if content exceeds the height */
}


/* side bar end */


/* Table Styling */
table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 1rem;
  text-align: left;
}

table thead tr {
  background-color: #2e3440; /* Header background color */
  color: white; /* Header text color */
  text-align: left;
}

table th, table td {
  border: 1px solid #ddd;
  padding: 8px;
}

table tbody tr:nth-child(even) {
  background-color: #f9f9f9; /* Alternate row color */
}

table tbody tr:hover {
  background-color: #f1f1f1; /* Highlight row on hover */
}



/* General Scrollbar Styles */
::-webkit-scrollbar {
  width: 12px; /* Width of the scrollbar */
}

::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1); /* Track color */
  border-radius: 6px; /* Rounded corners for the track */
}

::-webkit-scrollbar-thumb {
  background: #007bff; /* Scrollbar thumb color */
  border-radius: 6px; /* Rounded corners for the thumb */
  border: 2px solid rgba(255, 255, 255, 0.7); /* Adds a white border for a clean look */
}

::-webkit-scrollbar-thumb:hover {
  background: #0056b3; /* Darker shade on hover */
}

/* Styling for the Sidebar Scroll */
* {
  scrollbar-width: thin; /* Firefox support for thin scrollbars */
  scrollbar-color: #007bff rgba(0, 0, 0, 0.1); /* Colors for thumb and track in Firefox */
}

*::-webkit-scrollbar {
  width: 8px;
}

*::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #007bff, #0056b3); /* Gradient effect for sidebar thumb */
}

/* Styling for Main Content Scroll */
body {
  scrollbar-width: thin; /* Thin scrollbar for the main content */
  scrollbar-color: #ccc rgba(0, 0, 0, 0.1);
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
  background: #999;
}


/* Sidebar container styles */
.sidebar-container {
  max-width: 100%;
  background-color: #f8f9fa;
  border-right: 1px solid #ddd;
  height: 100vh;
  overflow-y: auto;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  padding: 20px;
  transition: transform 0.3s ease;
}

.sidebar-header {
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  border-bottom: 2px solid #ddd;
  padding-bottom: 10px;
}

.sidebar-content {
  padding-left: 10px;
}

/* Sidebar list styles */
.sidebar-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.sidebar-item {
  margin-bottom: 5px;
}

.sidebar-title {
  display: flex;
  align-items: center;
  padding: 8px 10px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  color: #333;
}

.sidebar-title:hover {
  background-color: #eef3f8;
}

.sidebar-icon {
  margin-right: 8px;
  font-weight: bold;
}

.sidebar-nested {
  margin-left: 15px;
  border-left: 2px solid #ddd;
  padding-left: 10px;
}

.sidebar-link {
  text-decoration: none;
  color: #007bff;
  font-size: 0.9rem;
}

.sidebar-link:hover {
  text-decoration: underline;
}


/* Active link styling */
.sidebar-item.active > .sidebar-title {
  background-color: #cce5ff;
  color: #004085;
}

.sidebar-item.active > .sidebar-link {
  font-weight: bold;
  color: #004085;
}
.linkHighlight {
  /* Neon glow properties */
  background-color: #ffeb3b; /* Bright yellow background */
  color: #000; /* Black text for contrast */
  border-radius: 0 10px 10px 0; /* Rounded edges */

  /* Neon glow effect */
  box-shadow: 0 0 10px #ffeb3b, 0 0 20px #ffeb3b, 0 0 30px #ffeb3b;
  text-shadow: 0 0 5px #ffeb3b, 0 0 10px #ffeb3b;

  /* Transition for smooth effect on hover */
  transition: box-shadow 0.3s ease, text-shadow 0.3s ease;
}

.linkHighlight:hover {
  /* Intensify the glow on hover */
  box-shadow: 0 0 15px #ffd700, 0 0 30px #ffd700, 0 0 45px #ffd700;
  text-shadow: 0 0 10px #ffd700, 0 0 20px #ffd700;
}

